.favorite {
    width: 12px;
    height: 12px;
}

.favorite>input,
.favorite>label>span {
    clip: rect(1px, 1px, 1px, 1px);
    position: absolute;
}

.star {
    --color-lemon: #fced08;
    --color-pastel-yellow: #f2f2b0;
    --color-pastel-purple: #d7acd4;
    --color-gray: #eeeeee;
    -webkit-transform: scale(2);
            transform: scale(2);
    -webkit-transform-origin: center;
            transform-origin: center;
}

.explosion {
    -webkit-transform: scale(0);
            transform: scale(0);
    fill: var(--color-pastel-purple);
    -webkit-transform-origin: center;
            transform-origin: center;
}

.star {
    fill: var(--color-gray);
}

.star.-checked {
    -webkit-animation: activate 200ms ease-in;
            animation: activate 200ms ease-in;
    fill: var(--color-pastel-yellow);
}

.star.-checked .explosion {

    /* "forwards" stops the animation from going back to its initial state */
    -webkit-animation: explode 400ms linear forwards;
            animation: explode 400ms linear forwards;
}

@-webkit-keyframes activate {
    30% {
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
    }

    50% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }

    80% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
}

@keyframes activate {
    30% {
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
    }

    50% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }

    80% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
}

/* This is probably totally wrong */
@-webkit-keyframes explode {
    10% {
        -webkit-transform: scale(-.2);
                transform: scale(-.2);
        opacity: 0;
    }

    50% {
        -webkit-transform: scale(-.6);
                transform: scale(-.6);
        opacity: .2;
    }

    70% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }

    100% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        opacity: 0;
    }
}
@keyframes explode {
    10% {
        -webkit-transform: scale(-.2);
                transform: scale(-.2);
        opacity: 0;
    }

    50% {
        -webkit-transform: scale(-.6);
                transform: scale(-.6);
        opacity: .2;
    }

    70% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }

    100% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        opacity: 0;
    }
}



